:root{
    
    --cc-primary: #E52926;
    --cc-primary-light: #EE7573;
    --cc-primary-dark: #970200;
    --cc-primary-disabled: #EED1D1;
    --cc-secondary: #F8F8F0;
    --cc-white: #FFFFFF;
    --cc-black: #000000;
    --jwe-black-light: #555555;
}

body {
    font-family: 'Silka', sans-serif;
}

.nav-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: transparent;
    position: fixed;
    width: 100%;
    height: 60px;
}

.nav-logo {
    height: 48px;
}

.lp-block1-wrapper, .lp-block2-wrapper, .lp-block3-wrapper, .lp-block4-wrapper {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.lp-block1-wrapper {
    background-color: var(--cc-primary);
    background-image: url(/src/assets/images/cc6.JPEG);
    background-position: center;
    background-size: cover;
}


.lp-block1-text {
}

.lp-block2-wrapper {
    background-color: var(--cc-secondary);
}

.lp-block3-wrapper {
    background-color: var(--cc-primary);
}

.lp-block4-wrapper {
    background-color: var(--cc-secondary);
}
